import { Component, OnInit, Input } from '@angular/core';
import {
  ActivatedRoute,
  Router,
  ParamMap,
  Params,
  NavigationEnd,
  PRIMARY_OUTLET
} from '@angular/router';
interface IBreadcrumb {
  label: string;
  params?: Params;
  url: string;
}
@Component({
  selector: 'app-breadcrumb',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.less']
})
export class TestHomeComponent implements OnInit {
  isAllDisplayDataChecked = false;
  isOperating = false;
  isIndeterminate = false;
  // 定义一个checkall 数据为空
  listOfDisplayData: Data[] = [];
  listOfAllData: Data[] = [];
  mapOfCheckedId: { [key: string]: boolean } = {};
  numberOfChecked = 0;

  currentPageDataChange($event: Data[]): void {
    console.log($event);
    console.log('执行1');
    this.listOfDisplayData = $event;
    this.refreshStatus();
  }

  refreshStatus(): void {
    console.log('执行2');
    console.log(this.listOfDisplayData);
    console.log(this.isAllDisplayDataChecked);
    this.isAllDisplayDataChecked = this.listOfDisplayData
      .filter(item => !item.disabled)
      .every(item => this.mapOfCheckedId[item.id]);
    this.isIndeterminate =
      this.listOfDisplayData
        .filter(item => !item.disabled)
        .some(item => this.mapOfCheckedId[item.id]) &&
      !this.isAllDisplayDataChecked;
    this.numberOfChecked = this.listOfAllData.filter(
      item => this.mapOfCheckedId[item.id]
    ).length;
  }

  checkAll(value: boolean): void {
    console.log('执行3');
    this.listOfDisplayData
      .filter(item => !item.disabled)
      .forEach(item => (this.mapOfCheckedId[item.id] = value));
    this.refreshStatus();
  }

  operateData(): void {
    console.log('执行4');
    this.isOperating = true;
    setTimeout(() => {
      this.listOfAllData.forEach(
        item => (this.mapOfCheckedId[item.id] = false)
      );
      this.refreshStatus();
      this.isOperating = false;
    }, 1000);
  }

  ngOnInit(): void {
    console.log('执行5');
    for (let i = 0; i < 100; i++) {
      this.listOfAllData.push({
        id: i,
        name: `Edward King ${i}`,
        age: 32,
        address: `London, Park Lane no. ${i}`,
        disabled: i % 2 === 0
      });
    }
    console.log(this.listOfAllData);
  }
}
export interface Data {
  id: number;
  name: string;
  age: number;
  address: string;
  disabled: boolean;
}
